import { AuthInterceptor } from './auth/auth.interceptor';
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { Router } from "@angular/router";
import { ToasterService } from '../../shared/toastrService/toaster.service';
import { timeStamp } from 'console';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  noAuthHeader = { headers: new HttpHeaders({ 'NoAuth': 'True' }) };

  constructor(
    private http: HttpClient,
    private router: Router,
    private toastr: ToasterService,
  ) {
  }
   postRequest(URL,data,header=false){
    if(header) return this.http.post(URL, data, this.getHeader())
    else return this.http.post(URL, data, this.noAuthHeader)
  }
  setToken(token) {
    localStorage.setItem('token', JSON.stringify(token));
  }

  getToken() {
    return JSON.parse(localStorage.getItem('token'));
  }

  deleteToken() {
    localStorage.removeItem('token');
  }

  getUserPayload() {
    var token = this.getToken();
    if (token) {
      var userPayload = atob(token['token'].split('.')[1]);
      return JSON.parse(userPayload);
    }
    else
      return null;
  }

  isLoggedIn(data:{role:[]}|any) {
    var userPayload = this.getUserPayload();
    if (userPayload){
      const role=this.getToken().userInfo.role;
      if(data.role==undefined)  return userPayload.exp > Date.now() / 1000;
      else if(data.role.indexOf(role)!=-1) return userPayload.exp > Date.now() / 1000;
    }
    else  return false;
  }
  isRightURL(role, urlList) {
    const d = urlList[0].path;
    switch (d) {
      case ('employee'): if (role == "admin") return true; else return false;
      case ('customer'): if (role == "admin" || role == "support") return true; else return false;
      case ('category'): if (role == "admin" || role == "support") return true; else return false;
      case ('product'): if (role == "admin" || role == "support") return true; else return false;
      case ('order'): if (role == "admin" || role == "support") return true; else return false;
      default: return true;
    }
  }
  getHeader() {
    const header = { headers: { Authorization: `Bearer ${this.getToken().token}` }, user: this.getToken().userInfo }
    return header;
  }
}
