import { UserService } from '../../../../shared/userService/user.service';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { ToasterService } from '../../../../shared/toastrService/toaster.service';
import { AfterViewInit, Component, ViewChild, OnInit } from '@angular/core';
import { environment } from '../../../../../environments/environment';
import { AngularCsv } from 'angular7-csv/dist/Angular-csv';
@Component({
  selector: 'app-supplier',
  templateUrl: './supplier.component.html',
  styleUrls: ['./supplier.component.css']
})
export class SupplierComponent implements OnInit {
  user;
  modalStatus = false;
  editStatus = false;
  addStatus = false;
  manageStatus = false;
  percent = 0;
  supplier;
  product = [];
  productOrigin = [];
  delKey;
  editKey;
  data = [];
  formGroup: FormGroup;
  key;
  srcCol=['name','detail'];
  constructor(
    private userService: UserService,
    private router: Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
  ) { }
  
  ngOnInit(): void {
    this.user = this.userService.getToken()['userInfo'];//set information
    this.refresh();//set all data to first value
    this.getItem();//get  information from service
  }
  //CRUD functions related to Service
  addItem() {//Create new 
    this.userService.postRequest('/api/supplier/create',this.formGroup.value,true).subscribe(
      res => {
        this.showSuccessToaster('success', '', 'success');
        this.refresh();
        this.getItem();
      },
      err => {
        this.handleError(err)
      }
    );
  }
  editItem() {//Update information
    this.userService.postRequest('/api/supplier/update',this.formGroup.value,true).subscribe(
      res => {
        this.showSuccessToaster('success', '', 'success')
        this.refresh();
        this.getItem();
      },
      err => {
        this.handleError(err)
      }
    );
  }
  deleteItem() {//Delete information
    this.userService.postRequest('/api/supplier/del',this.data[this.delKey],true).subscribe(
      res => {
        this.showSuccessToaster('success', '', 'success')
        this.refresh();
        this.getItem();
      },
      err => {
       this.handleError(err)
      }
    );
  }
  getItem() {//Get information
    this.userService.postRequest('/api/supplier/get',{},true).subscribe(
      res => {
        this.data = res['result'];
      },
      err => {
        this.handleError(err)
      }
    );
  }
   //functions related to table component
   add() {//function when "add" button click
   this.addStatus = true;
 }
 edit(id) {//function when "edit" button click
   this.editKey = this.findIndex(id,this.data);
   this.editStatus = true;
   let insertValue= this.data[this.editKey];
   delete insertValue.__v;
   this.formGroup.setValue(insertValue);
 }
 del(id) {//function when "delete" button click
   this.delKey = this.findIndex(id,this.data);
 }
 findIndex(id,datas) {//function to find index of table data
  for(let i=0;i<datas.length;i++){
    if(datas[i]._id===id) return i;
  }
  return 0;
 }
 applyFilter(arr){//search function of table
   if(!this.key) return true;
   for(let i=0;i<this.srcCol.length;i++){
     if(String(arr[this.srcCol[i]]).search(this.key)!=-1){
       return true;
     }
   }
   return false;
 }
 downloadCSV(){//download csv file when download button click
  if(this.data.length==0) return;
  const csvOption={
    fieldSeparator: ',',
    quoteStrings: '"',
    decimalseparator: '.',
    showLabels: true,
    showTitle: false,
    useBom: true,
    noDownload: false,
    headers: Object.keys(this.data[0])
  };
  new  AngularCsv(this.data, "SupplierList", csvOption);
}
uploadCSV(){//select input file button when upload button click
  document.getElementById('csv').click();
}
importCSV(event){//import csv file when download button click
    const file = event.target.files[0];
    let fileReader: FileReader = new FileReader();
    fileReader.readAsText(file);
    fileReader.onload = ev => {
      let csvdata = fileReader.result.toString();
      let body = {data:csvdata};
      this.userService.postRequest('/api/supplier/importCsv',body,true).subscribe(
        res => {
          this.showSuccessToaster('success', '', 'success')
          this.refresh();
          this.getItem();
        },
        err => {
         this.handleError(err)
          this.refresh();
        }
      );
    };
  event.target.value="";
}
setManage(name){//set product info of each supplier
  this.userService.postRequest('/api/product/get',{supplier:name},true).subscribe(
    res=>{
      this.manageStatus=true;
      this.supplier=name;
      this.product=res['result'];
      this.productOrigin=JSON.parse(JSON.stringify(res['result']));
    },
    err=>{
      this.handleError(err)
    }
  );
}
setPrice(){
 for(let i=0;i<this.product.length;i++){
   if(this.percent) {
     this.product[i].regularPrice = (100+this.percent)*this.productOrigin[i].regularPrice/100;
     this.product[i].salePrice = (100+this.percent)*this.productOrigin[i].salePrice/100;
    }
   else{
     this.product[i].salePrice = this.productOrigin[i].salePrice;
     this.product[i].salePrice = this.productOrigin[i].salePrice;
    }
 }
}
setOrigin(){//set criteria
  this.productOrigin=JSON.parse(JSON.stringify(this.product));
}
async saveManage(){//save all changed price
  for(let i=0;i<this.product.length;i++){
    const d = await this.userService.postRequest('/api/product/update',this.product[i],true).toPromise().then(
      res=>{
      }
    ).catch(
      err=>{
      }
    )
  }
}
cancelManage(){
  this.manageStatus=false;
  this.percent = 0;
  this.product = [];
  this.productOrigin = [];
}
raisePricePercent(){//raise percent

}

 //functions related to Information Modal component
 save() {//function when "save" button of Modal click
   if (this.addStatus == true) this.addItem();
   else if (this.editStatus == true) this.editItem();
 }
 cancel() {//function when "cancel" button of Modal click
   this.refresh();
 }
  refresh() {
    this.editStatus = false;
    this.addStatus = false;
    this.manageStatus = false;
    this.formGroup = this._formBuilder.group({
      _id:[''],
      name: ['Vintage', Validators.required],
      detail: ['Nader', Validators.required],
    });
  }
   //Toastr functions
   handleError(err){
    if(err.status==504)  this.showSuccessToaster('error', '', 'server is not responsing.');
    else         this.showSuccessToaster('error', '', err.error.message);
    this.refresh();
   }
   showSuccessToaster(style, msgtopic, msgContent) {
     this.toastr.show(style, msgtopic, msgContent);
   }
 }
 