import { UserService } from '../../../../shared/userService/user.service';
import { Router } from "@angular/router";
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { ToasterService } from '../../../../shared/toastrService/toaster.service';
import { AfterViewInit, Component, ViewChild, OnInit} from '@angular/core';
import { environment } from '../../../../../environments/environment';
import { AngularCsv } from 'angular7-csv/dist/Angular-csv';
@Component({
  selector: 'app-product',
  templateUrl: './product.component.html',
  styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {
  user;
  //for formInput
  category = [];
  brand = [];
  supplier = [];
  formGroup: FormGroup;
  //for carousel image display
  fileChange = false;
  media={
    _id:'',
    productID:'',
    image:[],
    video:'video url here'
  };
  activeImage = 0;
  carouselButtonDisplay = false;

  //for table
  key;
  editStatus = false;
  addStatus = false;
  delKey;
  editKey;
  data = [];
  srcCol=['title','category','brand','category','regularPrice','salePrice','currentStock','barcode','status'];
  constructor(
    private userService: UserService,
    private router: Router,
    private _formBuilder: FormBuilder,
    private toastr: ToasterService,
  ) { }
  
  ngOnInit(): void {
    this.user = this.userService.getToken()['userInfo'];//set user information
    this.set();//set category, brand , supplier
    this.getItem();//get  information
    this.refresh();//set all data
  }
   //CRUD functions related to Service
   addProduct() {//Create new Product
    this.userService.postRequest('/api/product/create',this.formGroup.value,true).subscribe(
      res => {
        this.media.productID=res['result']._id;
        this.handleSuccess()
      },
      err => {
        this.handleError(err)
      }
    );
  }
  editProduct() {//Update Product information
    this.userService.postRequest('/api/product/update',this.formGroup.value,true).subscribe(
      res => {
        this.handleSuccess()
      },
      err => {
        this.handleError(err)
      }
    );
  }
  deleteProduct() {//Delete Product information
    this.userService.postRequest('/api/product/del',this.data[this.delKey],true).subscribe(
      res => {
        this.handleSuccess()
      },
      err => {
       this.handleError(err)
      }
    );
  }
  getItem() {//Get Product information
    this.userService.postRequest('/api/product/get',{},true).subscribe(
      res => {
        this.data = res['result'];
      },
      err => {
        this.handleError(err)
      }
    );
  }
  addMedia() {//Create new Media
    this.userService.postRequest('/api/media/create',this.media,true).subscribe(
      res => {
        this.media=res['result'];
        this.showSuccessToaster('success', '', 'success')
      },
      err => {
        this.handleError(err)
      }
    );
  }
  editMedia() {//Update Media information
    this.userService.postRequest('/api/media/update',this.media,true).subscribe(
      res => {
        this.media=res['result'];
        this.showSuccessToaster('success', '', 'success')
      },
      err => {
        this.handleError(err)
      }
    );
  }
  getMediaOne(id) {//Update Media information
    this.userService.postRequest('/api/media/get',{productID:id},true).subscribe(
      res => {
        if(res['result'].length>0) this.media=res['result'][0]
        else this.media.productID=id;
      },
      err => {
        this.handleError(err)
      }
    );
  }
  set(){//set for category , brand, supplier
    this.userService.postRequest('/api/category/get', {},true).subscribe(
      res=>{
        this.category=res['result'];
      }
    );
    this.userService.postRequest('/api/brand/get', {},true).subscribe(
      res=>{
        this.brand=res['result'];
      }
    );
    this.userService.postRequest('/api/supplier/get',{},true).subscribe(
      res=>{
        this.supplier=res['result'];
      }
    );
  }
 //functions related to table component
 add() {//function when "add" button click
  this.refresh();
  window.scroll(0,0)
  this.addStatus = true;
 }
 edit(id) {//function when "edit" button click
   this.refresh();
   window.scroll(0,0)
   this.editStatus = true;
   this.editKey = this.findIndex(id,this.data);
   let insertValue= this.data[this.editKey];
   delete insertValue.__v;
   this.formGroup.setValue(insertValue);
   this.getMediaOne(insertValue._id);
 }
 del(id) {//function when "delete" button click
   this.activeImage=0;
   this.delKey = this.findIndex(id,this.data);
 }
 findIndex(id,datas) {//function to find index of table data
  for(let i=0;i<datas.length;i++){
    if(datas[i]._id===id) return i;
  }
  return 0;
 }
 applyFilter(arr){//search function of table
   if(!this.key) return true;
   for(let i=0;i<this.srcCol.length;i++){
     if(String(arr[this.srcCol[i]]).search(this.key)!=-1){
       return true;
     }
   }
   return false;
 }
 downloadCSV(){
  if(this.data.length==0) return;
  const csvOption={
    fieldSeparator: ',',
    quoteStrings: '"',
    decimalseparator: '.',
    showLabels: true,
    showTitle: false,
    useBom: true,
    noDownload: false,
    headers: Object.keys(this.data[0])
  };
  new  AngularCsv(this.data, "Productlist", csvOption);
}
uploadCSV(){
  document.getElementById('csv').click();
}
importCSV(event){
  const file = event.target.files[0];
    let fileReader: FileReader = new FileReader();
    fileReader.readAsText(file);
    fileReader.onload = ev => {
      let csvdata = fileReader.result.toString();
      let body = {data:csvdata};
      this.userService.postRequest('/api/product/importCsv',body,true).subscribe(
        res => {
          this.showSuccessToaster('success', '', res['message'])
          this.getItem();
        },
        err => {
         this.handleError(err)
        }
      );
    };
  event.target.value="";
}
//add and remove carousel
addCarousel(){
  document.getElementById('photo').click();
}
delCarousel(){
 this.media.image.splice(this.activeImage,1);
}
 //File manage function
 selectFile(event) {//function when file input "ok" button click. Here new carousel image is inserted.
 let reader = new FileReader();
 this.fileChange = true;
 const mimeType = event.target.files[0].type;
 const file = event.target.files[0];
 reader.readAsDataURL(file);
 reader.onload = (_event) => {
   this.media.image=this.putInMiddle(this.media.image,this.activeImage,reader.result)
 };
 event.target.value="";
}
//mouse events on carousel div
 mouseLeave(){
   this.carouselButtonDisplay=false;
}
 mouseenter(){
  this.carouselButtonDisplay=true;
}
 //functions related to Information Modal component
 saveProduct() {//function when "save" button of Modal click
   if(!this.formGroup.valid){
     this.showSuccessToaster('error','','Please provide content correctly');
     return
   }
   if (this.addStatus == true) this.addProduct();
   else if (this.editStatus == true) this.editProduct();
 }
 saveImage() {//function when "save" button of Modal click
 if (!this.media._id) this.addMedia();
 else this.editMedia();
 this.activeImage = 0;
}
 cancel() {//function when "cancel" button click
   this.refresh();
 }
 getSource(item){//get product image in table
  if(item.length>0)  return item[0]
  else return 'assets/img/costic/add-product-1.jpg';
 }
  refresh() {
    this.editStatus = false;
    this.addStatus = false;
    this.fileChange = false;
    this.activeImage = 0;
    this.media={
      _id:'',
      productID:'',
      image:[],
      video:'video url here'
    };
    this.formGroup = this._formBuilder.group({
      _id: [''],
      title: ['title of product', Validators.required],
      detail: [''],
      category: ['',Validators.required],
      regularPrice: [0, Validators.required],
      salePrice: [0, Validators.required],
      taxRate: [0, Validators.required],
      barcode: [0, Validators.required],
      brand: ['', Validators.required],
      supplier: ['', Validators.required],
      currency: ['USD', Validators.required],
      created_at: [new Date().toISOString()],
      currentStock: [0,Validators.required],
      minStock: [0,Validators.required],
      maxStock: [100,Validators.required],
      status: ['in stock', Validators.required],
    });
  }
  //Toastr functions
  handleError(err){
    if(err.status==400&&err.error.err.keyValue) this.showSuccessToaster('error', '', '' + Object.keys(err.error.err.keyValue)[0]+' is doublicated.');
    else if(err.status==504)  this.showSuccessToaster('error', '', 'server is not responsing.');
    else         this.showSuccessToaster('error', '', err.error.message);
  }
  handleSuccess(){
    this.showSuccessToaster('success', '', 'success')
    this.getItem();
  }
   showSuccessToaster(style, msgtopic, msgContent) {
     this.toastr.show(style, msgtopic, msgContent);
   }
   putInMiddle(arr,key,member){//insert memeber to in the middle of array
    const temp=[];
    for(let i=0;i<key;i++) temp.push(arr[i])
    temp.push(member);
    for(let i=key;i<arr.length;i++) temp.push(arr[i])
    return temp;
  }
 }